"use client";

import { useEffect, useState } from "react";
import ThemeSwitcher from "./ThemeSwitcher";

type Theme = "theme1" | "theme2" | "theme3" | "theme4";

export default function ClientThemeShell({ children }: { children: React.ReactNode }) {
  const [theme, setTheme] = useState<Theme>("theme1");

  useEffect(() => {
    const saved = (localStorage.getItem("theme") as Theme) || "theme1";
    setTheme(saved);
    document.documentElement.setAttribute("data-theme", saved);
  }, []);

  function applyTheme(t: Theme) {
    setTheme(t);
    localStorage.setItem("theme", t);
    document.documentElement.setAttribute("data-theme", t);
  }

  return (
    <>
      <header className="topbar">
        <div className="brand text-xl">Choose Your Design</div>
        <ThemeSwitcher theme={theme} onChange={applyTheme} />
      </header>

      {children}
    </>
  );
}
